"use client";

// ============================================================================
// Menux Intelligence â€” Slash Command Menu
// Ref: docs/Menux Intelligence.md â€” seÃ§Ã£o 3.2
// ============================================================================

import { useState, useMemo, useCallback, useEffect, useRef, startTransition } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { cn } from "@/lib/cn";
import { useAuthStore } from "@/stores/auth-store";
import { useIntelligenceStore } from "@/stores/intelligence-store";
import {
  getAvailableCommands,
  filterCommands,
} from "@/lib/intelligence-commands";
import type {
  SlashCommandDefinition,
  UserRoleIntelligence,
} from "@/types/intelligence";

interface SlashCommandMenuProps {
  /** Texto atual no input (para filtragem) */
  inputText: string;
  /** Se o menu estÃ¡ visÃ­vel */
  isVisible: boolean;
  /** Callback ao selecionar um comando */
  onSelect: (command: SlashCommandDefinition) => void;
  /** Callback ao fechar o menu */
  onClose: () => void;
}

export function SlashCommandMenu({
  inputText,
  isVisible,
  onSelect,
  onClose,
}: SlashCommandMenuProps) {
  const user = useAuthStore((s) => s.user);
  const { contextCard } = useIntelligenceStore();
  const [selectedIndex, setSelectedIndex] = useState(0);
  const menuRef = useRef<HTMLDivElement>(null);

  const role = (user?.role ?? "comercial") as UserRoleIntelligence;
  const hasCard = !!contextCard;

  // Comandos disponÃ­veis para o perfil
  const available = useMemo(
    () => getAvailableCommands(role),
    [role]
  );

  // Filtrar por texto digitado
  const filtered = useMemo(
    () => filterCommands(available, inputText),
    [available, inputText]
  );

  // Reset seleÃ§Ã£o quando filtro muda
  useEffect(() => {
    startTransition(() => {
      setSelectedIndex(0);
    });
  }, [inputText]);

  // NavegaÃ§Ã£o por teclado
  const handleKeyDown = useCallback(
    (e: KeyboardEvent) => {
      if (!isVisible || filtered.length === 0) return;

      switch (e.key) {
        case "ArrowDown":
          e.preventDefault();
          setSelectedIndex((prev) =>
            prev < filtered.length - 1 ? prev + 1 : 0
          );
          break;
        case "ArrowUp":
          e.preventDefault();
          setSelectedIndex((prev) =>
            prev > 0 ? prev - 1 : filtered.length - 1
          );
          break;
        case "Enter":
        case "Tab":
          e.preventDefault();
          if (filtered[selectedIndex]) {
            onSelect(filtered[selectedIndex]);
          }
          break;
        case "Escape":
          e.preventDefault();
          onClose();
          break;
      }
    },
    [isVisible, filtered, selectedIndex, onSelect, onClose]
  );

  useEffect(() => {
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, [handleKeyDown]);

  // Scroll item selecionado para view
  useEffect(() => {
    if (menuRef.current) {
      const selected = menuRef.current.querySelector(
        `[data-index="${selectedIndex}"]`
      );
      selected?.scrollIntoView({ block: "nearest" });
    }
  }, [selectedIndex]);

  if (!isVisible || filtered.length === 0) return null;

  return (
    <AnimatePresence>
      <motion.div
        ref={menuRef}
        initial={{ opacity: 0, y: 8 }}
        animate={{ opacity: 1, y: 0 }}
        exit={{ opacity: 0, y: 8 }}
        transition={{ duration: 0.15 }}
        className={cn(
          "absolute bottom-full left-0 right-0 mb-2 max-h-[320px] overflow-y-auto",
          "rounded-xl border border-white/14 bg-slate-950/96 shadow-2xl shadow-black/35 backdrop-blur-xl"
        )}
      >
        {/* Section header */}
        <div className="sticky top-0 border-b border-white/10 bg-slate-950/92 px-3 py-2 backdrop-blur-sm">
          <p className="text-[10px] font-semibold uppercase tracking-wider text-slate-400">
            Comandos rÃ¡pidos
          </p>
        </div>

        {/* Commands list */}
        <div className="p-1">
          {filtered.map((cmd, index) => {
            const needsCard = cmd.requiresCard && !hasCard;

            return (
              <button
                key={cmd.command}
                data-index={index}
                onClick={() => {
                  if (!needsCard) {
                    onSelect(cmd);
                  }
                }}
                onMouseEnter={() => setSelectedIndex(index)}
                className={cn(
                  "flex w-full items-center gap-3 rounded-lg px-3 py-2 text-left transition-colors",
                  index === selectedIndex
                    ? "bg-white/10"
                    : "hover:bg-white/7",
                  needsCard && "opacity-50 cursor-not-allowed"
                )}
                disabled={needsCard}
                title={
                  needsCard
                    ? "Requer card aberto"
                    : `${cmd.command} â€” ${cmd.description}`
                }
              >
                {/* Icon */}
                <span className="flex h-8 w-8 shrink-0 items-center justify-center rounded-lg border border-white/10 bg-white/8 text-base">
                  {cmd.icon}
                </span>

                {/* Content */}
                <div className="min-w-0 flex-1">
                  <div className="flex items-center gap-2">
                    <span className="font-body text-sm font-medium text-slate-100">
                      {cmd.label}
                    </span>
                    <code className="rounded border border-white/10 bg-white/8 px-1.5 py-0.5 font-mono text-[10px] text-slate-300">
                      {cmd.command}
                    </code>
                  </div>
                  <p className="truncate text-[11px] text-slate-400">
                    {cmd.description}
                  </p>
                </div>

                {/* Card required indicator */}
                {cmd.requiresCard && (
                  <span
                    className={cn(
                      "shrink-0 rounded-full px-1.5 py-0.5 text-[9px] font-medium",
                      hasCard
                        ? "bg-emerald-500/18 text-emerald-100"
                        : "bg-amber-500/18 text-amber-100"
                    )}
                  >
                    {hasCard ? "âœ… Card" : "âš ï¸ Requer card"}
                  </span>
                )}
              </button>
            );
          })}
        </div>
      </motion.div>
    </AnimatePresence>
  );
}
